/**
 * @module M/control/MapeaHistoryControl
 */

import MapeaHistoryImplControl from 'impl/mapeahistorycontrol';
import template from 'templates/mapeahistory';

export default class MapeaHistoryControl extends M.Control {
  /**
   * @classdesc
   * Main constructor of the class. Creates a PluginControl
   * control
   *
   * @constructor
   * @extends {M.Control}
   * @api stable
   */
  constructor() {
    // 1. checks if the implementation can create PluginControl
    if (M.utils.isUndefined(MapeaHistoryImplControl)) {
      M.exception('La implementación usada no puede crear controles MapeaHistoryControl');
    }
    // 2. implementation of this control
    const impl = new MapeaHistoryImplControl();
    super(impl, 'MapeaHistory');

    /**
     * Name to identify this control
     * @const
     * @type {string}
     * @public
     * @api stable
     */
    this.name = 'MapeaHistory';
  }

  /**
   * This function creates the view
   *
   * @public
   * @function
   * @param {M.Map} map to add the control
   * @api stable
   */
  createView(map) {
    this.map = map;
    return new Promise((success, fail) => {
      const html = M.template.compileSync(template);
      success(html);
    });
  }

  /**
   * This function is called on the control activation
   *
   * @public
   * @function
   * @api stable
   */
  activate() {
    super.activate();
  }
  /**
   * This function is called on the control deactivation
   *
   * @public
   * @function
   * @api stable
   */
  deactivate() {
    super.deactivate();
  }
  /**
   * This function gets activation button
   *
   * @public
   * @function
   * @param {HTML} html of control
   * @api stable
   */
  getActivationButton(html) {
    return html.querySelector('.m-mapeahistory button');
  }

  /**
   * This function compares controls
   *
   * @public
   * @function
   * @param {M.Control} control to compare
   * @api stable
   */
  equals(control) {
    return control instanceof MapeaHistoryControl;
  }
}
