/**
 * @module M/impl/control/MapeaHistoryControl
 */
export default class MapeaHistoryControl extends M.impl.Control {
  /**
   * This function adds the control to the specified map
   *
   * @public
   * @function
   * @param {M.Map} map to add the plugin
   * @param {HTMLElement} html of the plugin
   * @api stable
   */
  addTo(map, html) {
    super.addTo(map, html);
    this.facadeMap_ = map;
    const mapImpl = map.getMapImpl();
    let shouldUpdate = true;

    const updatePermalink = () => {
      if (!shouldUpdate) {
        // do not update the URL when the view was changed in the 'popstate' handler
        shouldUpdate = true;
        return;
      }

      const view = this.facadeMap_.getMapImpl().getView();
      const center = view.getCenter();
      const hash = `#map=
      ${view.getZoom()} /
      ${Math.round(center[0] * 100) / 100} /
      ${Math.round(center[1] * 100) / 100}`;

      const state = {
        center: view.getCenter(),
        resolution: view.getResolution(),
      };

      window.history.pushState(state, 'map', hash);
    };

    mapImpl.on('moveend', updatePermalink);

    // restore the view state when navigating through the history, see
    // https://developer.mozilla.org/en-US/docs/Web/API/WindowEventHandlers/onpopstate
    window.addEventListener('popstate', (event) => {
      if (M.utils.isUndefined(event.state) || event.state === null) {
        return;
      }
      const view = this.facadeMap_.getMapImpl().getView();
      view.animate({
        center: event.state.center,
        resolution: event.state.resolution,
        duration: 300,
      });

      shouldUpdate = false;
    });

    const buttonPrevious = html.querySelector('#m-mapeahistoryprevious-button');
    const buttonNext = html.querySelector('#m-mapeahistorynext-button');
    buttonPrevious.addEventListener('click', () => {
      window.history.back();
    });
    buttonNext.addEventListener('click', () => {
      window.history.forward();
    });
  }
}
